import React, { useState } from "react";

const Url = () => {
  const urlMapValues = {
    id: Number,
    longurl: String,
    shorturl: String,
  };
  const [shortUrl, setShortUrl] = useState("");
  const [longUrl, setLongUrl] = useState("");
  const [urlMap, setUrlMap] = useState([]);
  const [finalUrl, setFinalUrl] = useState("");
  const [id, setId] = useState(200000);

  const getShortUrl = () => {
    let newshortUrl = "";
    urlMap.forEach((url) => {
      if (url.longurl === longUrl) {
        newshortUrl = url.shorturl;
      }
    });
    if (newshortUrl === "") {
      let key = toBase62(id);
      newshortUrl = "https://myshorturl/" + key;
      setUrlMap([
        ...urlMap,
        {
          id: id,
          longurl: longUrl,
          shorturl: newshortUrl,
        },
      ]);
      setId(id + 1);
    }
    setShortUrl(newshortUrl);
    setFinalUrl(newshortUrl);
  };
  const getLongUrl = () => {
    let tempUrl = ""; 
    let key = shortUrl.substr(19);
    let id = fromBase62(key);
    urlMap.forEach((url) => {
      if (url.id === id) {
        //setLongUrl(url.longurl);
        tempUrl=url.longurl;
        setFinalUrl(url.longurl);
      }
    });
    if(!tempUrl){
       alert("Cannot find Url: "+shortUrl);
       setFinalUrl('');
    }
  };
  const toBase62 = (n) => {
    console.log(urlMap);
    if (n === 0) {
      return "0";
    }
    const digits =
      "0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ";
    let result = "";
    while (n > 0) {
      result = digits[n % digits.length] + result;
      n = parseInt(n / digits.length, 10);
    }
    console.log(result);
    return result;
  };

  const fromBase62 = (s) => {
    const digits =
      "0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ";
    let result = 0;
    for (var i = 0; i < s.length; i++) {
      let p = digits.indexOf(s[i]);
      if (p < 0) {
        return NaN;
      }
      result += p * Math.pow(digits.length, s.length - i - 1);
    }
    return result;
  };

  return (
    <div>
     <form>
      <div>
        <label>Enter Long Url: </label>
        <input
          id="longUrl"
          type="text"
          placeholder="Long URL"
          style={{ height: 30, width: 550, margin: 20 }}
          onChange={(e) => setLongUrl(e.target.value)}
        />
        <button type="reset" style={{height:30}} onClick={getShortUrl}>
          Get Short URL
        </button>
      </div>
      <div>
        <label>Enter Short Url: </label>
        <input
          id="shortUrl"
          type="text"
          placeholder="Short URL"
          style={{ height: 30, width: 550, margin: 20 }}
          onChange={(e) => setShortUrl(e.target.value)}
        />
        <button type="reset" style={{height:30}} onClick={getLongUrl} >
          Get Long URL
        </button>
      </div>
      {finalUrl && (
        <div>
          <label  style={{ height: 50}}>Your Url is: </label>
          < input
          type="text"
            style={{ height: 80, width: 600, margin: 20 }}
            readOnly={true}
            value={finalUrl}
          />
        </div>
      )}
      </form>
    </div>
  );
};

export default Url;
